<template>
  <div>
    <TypeNav />
    <div class="main">
      <div class="py-container">
        <!--bread-->
        <div class="bread">
          <ul class="fl sui-breadcrumb">
            <li>
              <a href="#">全部结果</a>
            </li>
          </ul>
          <ul class="fl sui-tag">
            <li class="with-x" v-if="searchParams.categoryName">
              {{ searchParams.categoryName }}
              <span @click="removeQuery">X</span>
            </li>
            <li class="with-x" v-if="searchParams.keyword">
              {{ searchParams.keyword }}
              <span @click="removeKeyword">X</span>
            </li>
            <li class="with-x" v-if="searchParams.trademark">
              {{ searchParams.trademark.split(":")[1] }}
              <span @click="removeTrademark">X</span>
            </li>
            <li
              class="with-x"
              v-for="(item, index) in searchParams.props"
              :key="item"
            >
              {{ item.split(":")[1] }}
              <span @click="removeProps(index)">X</span>
            </li>
          </ul>
        </div>

        <!--selector-->
        <SearchSelector
          @setTrademark="setTrademark"
          @setProps="setProps"
          :trademarkList="trademarkList"
          :attrsList="attrsList"
        />

        <!--details-->
        <div class="details clearfix">
          <div class="sui-navbar">
            <div class="navbar-inner filter">
              <ul class="sui-nav">
                <li
                  :class="
                    searchParams.order.split(':')[0] === '1' ? 'active' : ''
                  "
                  @click="changeOrder('1')"
                >
                  <a href="javascript:;">
                    综合
                    <i
                      class="iconfont"
                      style="font-size: 10px"
                      v-show="searchParams.order.split(':')[0] === '1'"
                      :class="
                        searchParams.order.split(':')[1] === 'desc'
                          ? ' icon-arrowdown'
                          : 'icon-upward'
                      "
                    ></i>
                  </a>
                </li>
                <li
                  :class="
                    searchParams.order.split(':')[0] === '2' ? 'active' : ''
                  "
                  @click="changeOrder('2')"
                >
                  <a href="javascript:;">
                    价格
                    <i
                      v-show="searchParams.order.split(':')[0] === '2'"
                      class="iconfont icon-arrowdown"
                      style="font-size: 10px"
                      :class="
                        searchParams.order.split(':')[1] === 'desc'
                          ? ' icon-arrowdown'
                          : 'icon-upward'
                      "
                    ></i>
                  </a>
                </li>
              </ul>
            </div>
          </div>
          <div class="goods-list">
            <ul class="yui3-g">
              <li class="yui3-u-1-5" v-for="item in goodsList" :key="item.id">
                <div class="list-wrap">
                  <div class="p-img">
                    <router-link
                      :to="{
                        name: 'detail',
                        params: {
                          skuId: item.id,
                        },
                      }"
                      ><img v-lazy="item.defaultImg"
                    /></router-link>
                  </div>
                  <div class="price">
                    <strong>
                      <em>¥</em>
                      <i>{{ item.price }}</i>
                    </strong>
                  </div>
                  <div class="attr">
                    <a
                      target="_blank"
                      href="item.html"
                      title="促销信息，下单即赠送三个月CIBN视频会员卡！【小米电视新品4A 58 火爆预约中】"
                      >{{ item.title }}</a
                    >
                  </div>
                  <div class="commit">
                    <i class="command">已有<span>2000</span>人评价</i>
                  </div>
                  <div class="operate">
                    <a
                      href="success-cart.html"
                      target="_blank"
                      class="sui-btn btn-bordered btn-danger"
                      >加入购物车</a
                    >
                    <a href="javascript:void(0);" class="sui-btn btn-bordered"
                      >收藏</a
                    >
                  </div>
                </div>
              </li>
            </ul>
          </div>
          <Pagination
            :pageNo="searchParams.pageNo"
            :totalPages="totalPages"
            :continuePage="continuePage"
            @changePage="changePage"
          />
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import SearchSelector from "./SearchSelector/SearchSelector";
import { reqSearchList } from "@/api";
export default {
  name: "Search",
  data() {
    return {
      searchParams: {
        category1Id: null,
        category2Id: null,
        category3Id: null,
        categoryName: "",
        keyword: "",
        order: "1:desc",
        pageNo: 1, //当前页码
        pageSize: 100,
        props: [],
        trademark: "",
      },
      attrsList: [],
      goodsList: [],
      trademarkList: [],
      totalPages: 0, //总条数
      continuePage: 3, //连续页码
    };
  },
  mounted() {
    this.getSearchList();
    // 请求地址栏数据
    this.initSearchParams();
  },
  methods: {
    // 发送请求 请求数据
    async getSearchList() {
      let res = await reqSearchList(this.searchParams);
      this.attrsList = res.attrsList;
      this.goodsList = res.goodsList;
      this.trademarkList = res.trademarkList;
      this.totalPages = res.totalPages;
    },
    // 将地址栏的值放在初始化数据中
    initSearchParams() {
      const { category1Id, category2Id, category3Id, categoryName } =
        this.$route.query;
      const { keyword } = this.$route.params;
      this.searchParams = {
        ...this.searchParams,
        category1Id,
        category2Id,
        category3Id,
        categoryName,
        keyword,
      };
    },
    // 移除面包屑query导航
    removeQuery() {
      this.searchParams = {
        ...this.searchParams,
        category1Id: null,
        category2Id: null,
        category3Id: null,
        categoryName: "",
      };
      // 因为要清空地址栏的参数所以要重新导航
      const params = this.$route.params;
      this.$router.push({
        name: "search",
        params,
      });
    },
    // 移除面包屑导航keyword
    removeKeyword() {
      // 将面包屑中的值清除
      this.searchParams.keyword = "";
      // 重新导航清除地址栏的参数
      const query = this.$route.query;
      this.$router.push({
        name: "search",
        query,
      });
      // 将header组件中的keyword值清空
      this.$bus.$emit("clearKeyword");
    },
    //接收品牌信息的自定义事件回调函数
    setTrademark(trademarkInfo) {
      this.searchParams.trademark = trademarkInfo;
    },
    //  删除品牌的面包屑事件函数
    removeTrademark() {
      this.searchParams.trademark = "";
    },
    // 接收平台属性自定义回调事件传参
    setProps(props) {
      // 修改平台属性重新发送请求
      this.searchParams.props.push(props);
    },
    // 删除平台属性面包屑导航
    removeProps(index) {
      this.searchParams.props.splice(index, 1);
    },
    // 改变排序类型
    changeOrder(type) {
      let oldtype = this.searchParams.order.split(":")[0];
      let oldOrder = this.searchParams.order.split(":")[1];
      if (type === oldtype) {
        let newOrder = type + ":" + (oldOrder === "desc" ? "asc" : "desc");
        this.searchParams.order = newOrder;
      } else {
        let newOrder = type + ":" + "desc";
        this.searchParams.order = newOrder;
      }
    },
    // 分页点击改变
    changePage(newPageNo) {
      this.searchParams.pageNo = newPageNo;
    },
  },
  components: {
    SearchSelector,
  },
  watch: {
    // 监听路由是否改变，如果改变将重新获取data中的数据
    $route() {
      this.initSearchParams();
    },
    // 监听初始对象是否被改变，如果被改变将重新发送请求
    searchParams: {
      deep: true,
      handler() {
        this.getSearchList();
      },
    },
  },
};
</script>

<style lang="less" scoped>
.main {
  margin: 10px 0;

  .py-container {
    width: 1200px;
    margin: 0 auto;

    .bread {
      margin-bottom: 5px;
      overflow: hidden;

      .sui-breadcrumb {
        padding: 3px 15px;
        margin: 0;
        font-weight: 400;
        border-radius: 3px;
        float: left;

        li {
          display: inline-block;
          line-height: 18px;

          a {
            color: #666;
            text-decoration: none;

            &:hover {
              color: #4cb9fc;
            }
          }
        }
      }

      .sui-tag {
        margin-top: -5px;
        list-style: none;
        font-size: 0;
        line-height: 0;
        padding: 5px 0 0;
        margin-bottom: 18px;
        float: left;

        .with-x {
          font-size: 12px;
          margin: 0 5px 5px 0;
          display: inline-block;
          overflow: hidden;
          color: #000;
          background: #f7f7f7;
          padding: 0 7px;
          height: 20px;
          line-height: 20px;
          border: 1px solid #dedede;
          white-space: nowrap;
          transition: color 400ms;
          cursor: pointer;

          i {
            margin-left: 10px;
            cursor: pointer;
            font: 400 14px tahoma;
            display: inline-block;
            height: 100%;
            vertical-align: middle;
          }

          &:hover {
            color: #28a3ef;
          }
        }
      }
    }

    .details {
      margin-bottom: 5px;

      .sui-navbar {
        overflow: visible;
        margin-bottom: 0;

        .filter {
          min-height: 40px;
          padding-right: 20px;
          background: #fbfbfb;
          border: 1px solid #e2e2e2;
          padding-left: 0;
          border-radius: 0;
          box-shadow: 0 1px 4px rgba(0, 0, 0, 0.065);

          .sui-nav {
            position: relative;
            left: 0;
            display: block;
            float: left;
            margin: 0 10px 0 0;

            li {
              float: left;
              line-height: 18px;

              a {
                display: block;
                cursor: pointer;
                padding: 11px 15px;
                color: #777;
                text-decoration: none;
              }

              &.active {
                a {
                  background: #e1251b;
                  color: #fff;
                }
              }
            }
          }
        }
      }

      .goods-list {
        margin: 20px 0;

        ul {
          display: flex;
          flex-wrap: wrap;

          li {
            height: 100%;
            width: 20%;
            margin-top: 10px;
            line-height: 28px;

            .list-wrap {
              .p-img {
                padding-left: 15px;
                width: 215px;
                height: 255px;

                a {
                  color: #666;

                  img {
                    max-width: 100%;
                    height: auto;
                    vertical-align: middle;
                  }
                }
              }

              .price {
                padding-left: 15px;
                font-size: 18px;
                color: #c81623;

                strong {
                  font-weight: 700;

                  // i {
                  //   margin-left: -5px;
                  // }
                }
              }

              .attr {
                padding-left: 15px;
                width: 85%;
                overflow: hidden;
                margin-bottom: 8px;
                min-height: 38px;
                cursor: pointer;
                line-height: 1.8;
                display: -webkit-box;
                -webkit-box-orient: vertical;
                -webkit-line-clamp: 2;

                a {
                  color: #333;
                  text-decoration: none;
                }
              }

              .commit {
                padding-left: 15px;
                height: 22px;
                font-size: 13px;
                color: #a7a7a7;

                span {
                  font-weight: 700;
                  color: #646fb0;
                }
              }

              .operate {
                padding: 12px 15px;

                .sui-btn {
                  display: inline-block;
                  padding: 2px 14px;
                  box-sizing: border-box;
                  margin-bottom: 0;
                  font-size: 12px;
                  line-height: 18px;
                  text-align: center;
                  vertical-align: middle;
                  cursor: pointer;
                  border-radius: 0;
                  background-color: transparent;
                  margin-right: 15px;
                }

                .btn-bordered {
                  min-width: 85px;
                  background-color: transparent;
                  border: 1px solid #8c8c8c;
                  color: #8c8c8c;

                  &:hover {
                    border: 1px solid #666;
                    color: #fff !important;
                    background-color: #666;
                    text-decoration: none;
                  }
                }

                .btn-danger {
                  border: 1px solid #e1251b;
                  color: #e1251b;

                  &:hover {
                    border: 1px solid #e1251b;
                    background-color: #e1251b;
                    color: white !important;
                    text-decoration: none;
                  }
                }
              }
            }
          }
        }
      }

      .page {
        width: 733px;
        height: 66px;
        overflow: hidden;
        float: right;

        .sui-pagination {
          margin: 18px 0;

          ul {
            margin-left: 0;
            margin-bottom: 0;
            vertical-align: middle;
            width: 490px;
            float: left;

            li {
              line-height: 18px;
              display: inline-block;

              a {
                position: relative;
                float: left;
                line-height: 18px;
                text-decoration: none;
                background-color: #fff;
                border: 1px solid #e0e9ee;
                margin-left: -1px;
                font-size: 14px;
                padding: 9px 18px;
                color: #333;
              }

              &.active {
                a {
                  background-color: #fff;
                  color: #e1251b;
                  border-color: #fff;
                  cursor: default;
                }
              }

              &.prev {
                a {
                  background-color: #fafafa;
                }
              }

              &.disabled {
                a {
                  color: #999;
                  cursor: default;
                }
              }

              &.dotted {
                span {
                  margin-left: -1px;
                  position: relative;
                  float: left;
                  line-height: 18px;
                  text-decoration: none;
                  background-color: #fff;
                  font-size: 14px;
                  border: 0;
                  padding: 9px 18px;
                  color: #333;
                }
              }

              &.next {
                a {
                  background-color: #fafafa;
                }
              }
            }
          }

          div {
            color: #333;
            font-size: 14px;
            float: right;
            width: 241px;
          }
        }
      }
    }
  }
}
</style>